<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <section class="van-doc-demo-section">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

        <van-list class="my_datalistStyle" v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <van-cell v-for="item in list" :label="item.date" :title="item.title" :clickable="true" :to="'/mall/gtja/financialTimes/' + item.id"/>
        </van-list>

      </van-pull-refresh>
    </section>

  </div>
</template>

<script>
  export default {
    name: "financialTimes",
    data() {
      return {
        title: '结构金融日报',
        isLoading: false,
        loading: false,
        finished: false,
        list: []
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
          this.count++;
        }, 500);
      },
      onLoad() {
        setTimeout(() => {
          for (let i = 0; i < 10; i++) {
            this.list.push(
              {id: i, title: '关于红星美凯龙家居集团股份有限公司回购H股股份并注销及减少注册资本的公告关于红星美凯龙家居集团股份有限公司回购H股股份并注销及减少注册资本的公告', date: '2018-09-28'})
          }
          // 加载状态结束
          this.loading = false;

          // 数据全部加载完成
          if (this.list.length >= 10) {
            this.finished = true;
          }
        }, 500);
      }
    }
  }
</script>

<style scoped>

</style>



